.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.wrapper {
  width: 450px;
}

.wrapper article {
  width: 100%;
}

h1 {
  margin-bottom: 0;
}

h3 {
  margin-top: 0;
}

img {
  border: none;
}
