.card {
  width: 600px;
  background: white;
  border-radius: 4px;
  border: 1px solid #ccc;
  display: flex;
  margin: 15px auto;
  max-height: 140px;
}

.info {
  text-align: left;
  padding-left: 10px;
}

.name {
  margin-bottom: 3px;
  color: black;
  font-size: 21px;
}

.email {
  margin: 0;
  color: rgba(0, 0, 0, 0.5);
  font-size: 12px;
}

.desc {
  color: black;
  font-size: 15px;
}
